.company_info_box {
  width: 50%;
  height: 40vh;
  margin: 20rem auto;
  border-radius: 5rem;
  background: linear-gradient(145deg, #cacaca, #f0f0f0);
  box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
  padding: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.company_info_box p {
  color: black;
}
